//import store from '@/store';
import Search from "@/components/Search.vue";
import Step from "@/components/Step.vue";

//import Guide from "@/components/Guide.vue";

export default {
  components: {
    Search,
    Step,
    //Guide,
  },
  data() {
    return {
      width: 0,
      height: 0,
      isTest: true,
      isActive: true,
      isMenu: false
    };
  },
  methods: {
    handleResize() {
      this.width = window.innerWidth;
      this.height = window.innerHeight;
      //this.boxSize = ((this.window.width - (16 * 2)) / 8) * 2 ;
    },
    fn_scroll() {
      this.$store.commit("scrollY", window.scrollY);
    },
    fn_searchActive: function() {
      if (this.searchActive) {
        this.$store.commit("searchActive", false);
      } else {
        this.$store.commit("searchActive", true);
      }
    },
    fn_menu: function() {
      if (!this.isMenu) {
        this.isMenu = true;
      } else {
        this.isMenu = false;
      }
    },
    fn_logout: function() {
      console.log("logout");
    },
    fn_error: function(event) {
      console.log(event);
    },
    Upper: function(value) {
      return value.toUpperCase();
    },
    Lower: function(value) {
      return value.toLowerCase();
    }
  },
  created() {
    //console.log('App created', this.$route.name)
    window.addEventListener("resize", this.handleResize);
    window.addEventListener("scroll", this.fn_scroll);
  },
  destroyed() {
    window.removeEventListener("resize", this.handleResize);
    window.removeEventListener("scroll", this.fn_scroll);
  },
  computed: {
    cartItemsLength() {
      return this.$store.state.cartProducts.length;
    },
    searchActive() {
      return this.$store.state.searchActive;
    },
    scrollY() {
      return this.$store.state.scrollY;
    }
  },
  watch: {
    $route(to, from) {
      console.log(from.name, " --> ", to.name);
    }
  },
  filters: {
    // Filter definitions
    Upper(value) {
      return value.toUpperCase();
    }
  }
};
</script>
