import Vue from 'vue'
import VueRouter from 'vue-router'
import NotFound from '@/views/NotFound.vue'
import Home from '@/views/Home.vue'
import Cart from '@/views/Cart.vue'
import Orders from '@/views/Orders.vue'
import Account from '@/views/Account.vue'

import Process from '@/views/Process.vue'
import Model from '@/components/Model.vue'
import Type from '@/components/Type.vue'
import Book from '@/components/Book.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    name: 'NotFound',
    component: NotFound
  },
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  {
    path: '/orders',
    name: 'Orders',
    component: Orders
  },
  {
    path: '/account',
    name: 'Account',
    component: Account
  },
  {
    path: '/process/:products',
    component: Process,
    children: [
      {
        path: '',
        name: 'Model',
        component: Model
      }
    ]
  },
  {
    path: '/process/:products/:model',
    component: Process,
    children: [
      {
        path: '',
        name: 'Type',
        component: Type
      }
    ]
  },
  {
    path: '/process/:products/:model/:type',
    component: Process,
    children: [
      {
        path: '',
        name: 'Book',
        component: Book
      }
    ]
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
