<template>
  <keep-alive>
    <transition :name="transitionName">
      <router-view />
    </transition>
  </keep-alive>
</template>

<script>
// @ is an alias to /src

export default {
  name: "Parts",
  components: {},
  data() {
    return {
      transitionName: "fade",
      groupName: null,
      items: []
    };
  },
  created() {},
  computed: {},
  methods: {
    isParentChild(to, from) {
      const toSplit = to.path.split("/");
      const fromSplit = from.path.split("/");
      const toDepth = toSplit.length;
      const fromDepth = fromSplit.length;

      if (
        fromSplit[fromDepth - 1] === toSplit[toDepth - 2] ||
        toSplit[toDepth - 1] === fromSplit[fromDepth - 2] ||
        fromSplit[fromDepth - 1] === toSplit[toDepth - 3] ||
        toSplit[toDepth - 1] === fromSplit[fromDepth - 3]
      ) {
        return true;
      }
      return false;
    }
  },
  watch: {
    $route(to, from) {
      const toDepth = to.path.split("/").length;
      const fromDepth = from.path.split("/").length;
      let isSameLevel = toDepth === fromDepth;
      let isFromOrToHome =
        from.path === "/" || from.path === `/` || to.path === `/`;

      // check for direct hierarchy
      let isParentChild = this.isParentChild(to, from);
      // when same level, => fade
      // when parent-child, => swipe
      if (isSameLevel || !isParentChild || isFromOrToHome) {
        this.transitionName = "fade";
      } else {
        this.transitionName =
          toDepth < fromDepth ? "swipe-right" : "swipe-left";
      }
    }
  }
};
</script>

<style scoped lang="scss">
.contents {
  position: relative;
  .container-group {
    background-color: #fff;

    left: 0;
    top: 0;
  }
}
</style>